import React, { useState } from "react";
import styles from "./PostsOfBoard.module.css";
import { useSelector } from "react-redux";
import { selectUser } from "../../features/userSlice";
import { makeStyles } from "@material-ui/core/styles";
import Avatar from "@material-ui/core";
import { AccountCircle, Delete, Edit } from "@material-ui/icons";
import { Button } from "@material-ui/core";
import axios from "axios";

interface PROPS {
  gametitle: string;
  posttitle: string;
  postId: string;
  text: string;
  numOfMember: number;
  timestamp: any;
  timestampEdit: any;
  uid: string;
  username: string;
  profileId: string;
  avatar: string;
  onReload: () => void;
  setScreenMode: (value: string) => void;
  setUidOfProfileMode: (value: string) => void;
}

const useStyles = makeStyles({
  userIcon: {
    fontSize: "45px",
  },
});

const PostsOfBoard: React.FC<PROPS> = (props) => {
  const [isEditMode, setIsEditMode] = useState(false);
  const [newGameTitle, setNewGameTitle] = useState("");
  const [newPostTitle, setNewPostTitle] = useState("");
  const [newText, setNewText] = useState("");

  const user = useSelector(selectUser);
  const classes = useStyles();

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
  };

  const handleChangeScreenMode = (newValue: string) => {
    props.setScreenMode(newValue);
  };

  //
  const editPost = async () => {
    if (
      newGameTitle != props.gametitle ||
      newPostTitle != props.posttitle ||
      newText != props.text
    ) {
      await axios.put("http://localhost:5000/post/board/edit", {
        id: props.postId,
        gameTitle: newGameTitle,
        postTitle: newPostTitle,
        postMsg: newText,
      });
    }
    setIsEditMode(false);
    props.onReload();
  };

  //
  const deletePost = async () => {
    await axios.delete("http://localhost:5000/post/board/delete", {
      params: {
        id: props.postId,
      },
    });
    props.onReload();
    console.log("executed!");
  };

  return (
    <div className={styles.posts_of_board}>
      <div className={styles.icon_name_id}>
        <span
          onClick={() => {
            props.uid == user.uid
              ? handleChangeScreenMode("MyProfile")
              : handleChangeScreenMode("UserProfile");
            props.setUidOfProfileMode(props.uid);
          }}
        >
          {props.avatar == "" ? (
            <AccountCircle className={classes.userIcon} />
          ) : (
            <></>
          )}
        </span>
        <div className={styles.name_id}>
          <p className={styles.name}>{props.username}</p>
          <p className={styles.id}>{props.profileId}</p>
        </div>
      </div>
      <div className={styles.timestamp}>
        <p>{new Date(props.timestamp).toLocaleString()}</p>
        {props.timestampEdit && (
          <p>{new Date(props.timestampEdit).toLocaleString()}</p>
        )}
      </div>
      {isEditMode ? (
        <form onSubmit={handleSubmit}>
          <input
            type="text"
            value={newGameTitle}
            onChange={(e) => setNewGameTitle(e.target.value)}
          />
          <input
            type="text"
            value={newPostTitle}
            onChange={(e) => setNewPostTitle(e.target.value)}
          />
          <textarea
            value={newText}
            onChange={(e) => setNewText(e.target.value)}
          />
          <Button onClick={editPost}>SEND</Button>
        </form>
      ) : (
        <div className={styles.texts}>
          <p>{props.gametitle}</p>
          <p>{props.posttitle}</p>
          <p>{props.numOfMember}</p>
          <p>{props.text}</p>
        </div>
      )}

      {props.uid == user.uid && (
        <div className={styles.btn_area}>
          <Button
            onClick={
              isEditMode
                ? () => {
                    setIsEditMode(false);
                  }
                : () => {
                    setIsEditMode(true);
                    setNewGameTitle(props.gametitle);
                    setNewPostTitle(props.posttitle);
                    setNewText(props.text);
                  }
            }
          >
            <Edit />
          </Button>
          <Button onClick={deletePost}>
            <Delete />
          </Button>
        </div>
      )}
    </div>
  );
};

export default PostsOfBoard;
